.card {
  box-shadow: 0 10px 40px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  border-radius: 10px;
  width: 200px;
  height: 300px;
  margin-left: auto;
  background-color: white;
  padding: 2rem;
  border: 10px solid #d9d6d6;
  position: fixed;
}

.card:last-child{

}

.card:not(:last-child){
  margin-right: -18rem;
}

.cardDiv{
  display: flex;
  width:300px;
  margin-left: auto;
  margin-right: auto;
  left: 20%;
  margin-top: auto;
}

body{
  overflow: hidden;
  place-items: center;
}
